.customers{
  padding-top: 60px;
  padding-bottom: 60px;

  margin-bottom: -48px;

  background-size: cover;
  background-repeat: no-repeat;
  background-position: bottom;

  background-image: url(../images/icons/customers-bg-sm.svg);
 
  @media screen and (min-width: 768px) {
    margin-bottom: -70px;
    padding-top: 60px;
    padding-bottom: 120px;
  
  background-image: url(../images/icons/customers-bg-md.svg);
  background-size: 100% 100%;
    }
  
    @media screen and (min-width: 1280px) {
      padding-bottom: 230px;
      margin-bottom: -10px;
      padding-top: 100px;
    
        background-image: url(../images/icons/customers-bg-lg.svg);
        background-size: 100% 100%;
      }
  }
