.customer__title{ 
  margin-bottom: 20px;

    font-weight: 700;
    font-size: 28px;
    line-height: 1.18;

    color: var(--title-text-color);

    @media screen and (min-width: 768px) {

        font-size: 42px;
        line-height: 1.2;
}
}
.customer__subtitle{
  margin-bottom: 32px;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.14;

  color: var(--title-text-color);

  @media screen and (min-width: 768px) {
      max-width: 656px;
      font-size: 16px;
  }
}
.customers__ul{
  margin: 0;
  padding: 0;
  list-style: none;
  //margin-bottom: 28px;
