  @media screen and (min-width: 768px) {
    display: flex;
    gap: 32px;

    //margin-bottom: 40px;
  }

  @media screen and (min-width: 1280px) {
    gap: 139px;

    //margin-bottom: 32px;
  }
}

.customers__fon{
