.backdrop-header {
  z-index: 1000;
  width: 100%;
  height: 100%;
  opacity: 1;
  visibility: visible;
  pointer-events: initial;
  background: rgba(0, 0, 0, .2);
  justify-content: center;
  align-items: center;
  transition: opacity .6s ease-out, visibility .6s ease-out;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop-header.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.backdrop-header.is-hidden .modal {
  opacity: 0;
  transform: translate(-50%, -50%)scale(.3)rotate(1turn);
}

.modal-header {
  width: 458px;
  height: 449px;
  opacity: 1;
  background-color: #ffe550;
  border-radius: 30px;
  padding: 32px;
  transition: transform .4s ease-in-out .2s, opacity .4s ease-in-out .2s;
  position: relative;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .06);
}

.modal-btn {
  width: 32px;
  height: 32px;
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 50%;
